.hotels-container {
  border: 2px solid red;
  display: flex;
  justify-content: center;
  height: 50vw;
  /* overflow: hidden; */
}

.hotels-img {
  border: 2px dashed green;
  flex: none;
}

.prevBtn {
  cursor: pointer;
  position: relative;
  /* top: 70%; */
  width: auto;
  padding: 16px;

  font-size: large;
  transition: 0.6s ease-out;
  border-radius: 0 5px 5px 0;
}

.nextBtn {
  cursor: pointer;
  padding: 16px;
  font-size: large;
  /* margin-top: -20px; */
  transition: 0.6s ease-out;
  border-radius: 0 5px 5px 0;
}
